import { useState } from "react";
import { useContentDispatch, useContentState } from "../editor/contentContext";
import { BSON } from "realm-web";

const styles = {
  cursor: "pointer",
  //   transform: `translate(${drag.translation.x}px, ${drag.translation.y}px)`,
  //   transition: drag.isDragging ? "none" : "transform 500ms",
  //   zIndex: drag.isDragging ? 2 : 1,
  position: "absolute",
  border: "1px solid red",
  borderRadius: "15px",
  rotate: "-5deg",
  padding: "5px",
  fontSize: "10px",
};

export function ContentAddIndex(props) {
  const noteId = props.noteId;
  const cardId = props.cardId;
  const contentDispatch = useContentDispatch();
  const [newContent, setNewContent] = useState("");
  const [openIndex, setOpenIndex] = useState(false);
  const contentState = useContentState();

  const handleChange = (event) => {
    setNewContent(event.target.value);
  };

  const addContent = () => {
    if (newContent.length > 0) {
      contentDispatch({
        type: "ADD_CONTENT",
        contentInfo: newContent,
        noteId: noteId,
        cardId: cardId,
      });
      setNewContent("");
    }
    return;
  };

  const addImageContent = () => {
    if (postImage.myFile !== null) {
      contentDispatch({
        type: "ADD_IMAGE_CONTENT",
        contentInfo: postImage.myFile,
        noteId: noteId,
        cardId: cardId,
      });
      console.log("image content => mongodb");
    } else {
      console.log("postImage.myFile is null!");
    }
  };

  const openExtraIndex = () => {
    setOpenIndex(true);
  };

  const closeExtraIndex = () => {
    setOpenIndex(false);
  };

  const [postImage, setPostImage] = useState({
    myFile: null,
  });

  const convertToBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        resolve(fileReader.result);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  };

  const handleFileUpload = async (e) => {
    const file = e.target.files[0];
    const maxFileSize = 20000; // 1MB = 10^6, 1KB = 10^3
    if (file.size > maxFileSize) {
      console.log("    ..");
    } else {
      const base64 = await convertToBase64(file);
      setPostImage({ ...postImage, myFile: base64 });
    }
  };

  return (
    <div style={styles}>
      {openIndex ? (
        <>
          <input
            type="text"
            name="content"
            onChange={handleChange}
            value={newContent}
          />
          <button onClick={addContent}>add</button>
          <button onClick={closeExtraIndex}>cancel</button>
          <button
            onClick={() => {
              console.log(contentState);
            }}
          >
            state
          </button>
          <p>20kb    </p>
          <input
            type="file"
            label="Image"
            name="myFile"
            accept=".jpeg, .png, .jpg"
            onChange={(e) => handleFileUpload(e)}
          />

          <button onClick={addImageContent}>Submit</button>
          <button onClick={() => console.log(postImage)}>base</button>
        </>
      ) : (
        <p onClick={openExtraIndex}> </p>
      )}
    </div>
  );
}
