import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useCardDispatch, useCardState } from "../editor/cardContext";
import { ContentProvider, useContentDispatch } from "../editor/contentContext";

import { useThemeState } from "../editor/themeContext";
import {
  CardContainer,
  CardEditMode,
  CardCover,
  CardEditModeButton,
  CardEditInput,
  CardEditDoneButton,
  CardEditCancelButton,
  CardRemoveButton,
  CardAndContentContainer,
} from "../styled-component/cardStyle";
import { ContentAddIndex } from "./contentExtraIndex";
import ContentsList from "./contentList";

export default function Card({ data }) {
  const theme = useThemeState();
  const param = useParams();
  const noteId = param.id;
  const cardTitle = data.cardTtitle;
  const cardId = data.cardId;
  const [contentsPage, setContentsPage] = useState(false);
  const [writtenCardTitle, setWrittenCardTitle] = useState(data.cardTitle);
  const [editedCardName, setEditedCardName] = useState(writtenCardTitle);
  const [editing, setEditing] = useState(false);
  const cardState = useCardState();
  const cardDispatch = useCardDispatch();
  const contentDispatch = useContentDispatch();

  const handleChange = (event) => {
    setEditedCardName(event.target.value);
  };

  const turnEditorOn = () => {
    setEditing(true);
  };

  const turnEditorOff = () => {
    setEditing(false);
    setEditedCardName(writtenCardTitle);
  };

  const editCard = () => {
    setWrittenCardTitle(editedCardName);
    cardDispatch({
      type: "EDIT_CARD",
      noteId: noteId,
      cardId: cardId,
      title: editedCardName,
    });
    setEditing(false);
  };

  const removeCard = () => {
    cardDispatch({ type: "REMOVE_CARD", noteId: noteId, cardId: cardId });
  };

  const openAndCloseContentsPage = () => {
    if (contentsPage) {
      setContentsPage(false);
    } else if (!contentsPage) {
      setContentsPage(true);
    }
  };

  return (
    <>
      <ContentProvider>
        <CardAndContentContainer>
          <CardContainer theme={theme}>
            {editing ? (
              <>
                <CardEditMode theme={theme}>
                  <CardEditInput
                    type="text"
                    value={editedCardName}
                    onChange={handleChange}
                  />
                  <CardEditDoneButton theme={theme} onClick={editCard}>
                    done
                  </CardEditDoneButton>
                  <CardEditCancelButton theme={theme} onClick={turnEditorOff}>
                    cancel
                  </CardEditCancelButton>
                  <CardRemoveButton theme={theme} onClick={removeCard}>
                    remove
                  </CardRemoveButton>
                </CardEditMode>
              </>
            ) : (
              <>
                <CardCover onClick={openAndCloseContentsPage}>
                  {writtenCardTitle}
                </CardCover>
                {contentsPage ? (
                  <></>
                ) : (
                  <CardEditModeButton theme={theme} onClick={turnEditorOn}>
                    card edit
                  </CardEditModeButton>
                )}
              </>
            )}
          </CardContainer>
          {contentsPage ? (
            <ContentsList noteId={noteId} cardId={cardId} />
          ) : (
            <></>
          )}
        </CardAndContentContainer>
      </ContentProvider>
    </>
  );
}
