import { useState } from "react";
import { useContentDispatch } from "../editor/contentContext";
import { useThemeState } from "../editor/themeContext";
import {
  ContentContainer,
  ContentImgContainer,
  ContentCover,
  ContentEditMode,
  ContentImgEditMode,
  ContentEditModeButton,
  ContentEditInput,
  ContentEditDoneButton,
  ContentRemoveButton,
  ContentEditCancelButton,
  ImgContentRemoveButton,
} from "../styled-component/contentStyle";

export default function Content(props) {
  const { contentInfo, contentId, isImage } = props.data;
  const noteId = props.noteId;
  const cardId = props.cardId;

  const theme = useThemeState();
  const contentDispatch = useContentDispatch();

  const [open, setOpen] = useState(false);
  const [editing, setEditing] = useState(false);

  const [contentInfoText, setContentInfoText] = useState(contentInfo);
  const [editedContentInfo, setEditedContentInfo] = useState(contentInfo);

  const handleChange = (event) => {
    setEditedContentInfo(event.target.value);
  };

  const openContent = () => {
    if (!open) {
      return setOpen(true);
    } else if (open) {
      return setOpen(false);
    }
  };

  const onEditing = () => {
    setEditing(true);
  };

  const offEditing = () => {
    setEditing(false);
  };

  const editContent = () => {
    // setContentInfoText(editedContentInfo);
    // if (editedContentInfo.length > 0) {
    //   contentDispatch({
    //     type: "EDIT_CONTENT",
    //     contentId: contentId,
    //     contentInfo: editedContentInfo,
    //   });
    //   setEditing(false);
    // }
  };

  const removeContent = () => {
    // contentDispatch({ type: "REMOVE_CONTENT", contentId: contentId });
  };

  return (
    <>
      {/* <button
        onClick={() => {
          console.log(noteId, cardId, contentId, isImage);
        }}
      >
        onon
      </button> */}
      <ContentContainer theme={theme} open={open}>
        {isImage ? (
          <>
            <ContentImgEditMode>
              <ContentImgContainer
                onClick={openContent}
                open={open}
                src={contentInfo}
              />
              {open ? (
                <></>
              ) : (
                <ImgContentRemoveButton onClick={removeContent}>
                  remove
                </ImgContentRemoveButton>
              )}
            </ContentImgEditMode>
          </>
        ) : (
          <>
            {editing ? (
              <ContentEditMode>
                <ContentEditInput
                  type="text"
                  value={editedContentInfo}
                  onChange={handleChange}
                />
                <ContentEditDoneButton onClick={editContent}>
                  edit done
                </ContentEditDoneButton>
                <ContentEditCancelButton onClick={offEditing}>
                  edit cancel
                </ContentEditCancelButton>
                <ContentRemoveButton onClick={removeContent}>
                  remove
                </ContentRemoveButton>
              </ContentEditMode>
            ) : (
              <>
                <ContentCover>{contentInfoText}</ContentCover>
                <ContentEditModeButton onClick={onEditing}>
                  content edit
                </ContentEditModeButton>
              </>
            )}
          </>
        )}
      </ContentContainer>
    </>
  );
}
