import { useEffect, useState } from "react";
import { getContentFromNathan } from "../editor/api";
import { useContentDispatch, useContentState } from "../editor/contentContext";
import Content from "./content";
import { ContentListContainer } from "../styled-component/contentStyle";
import { ContentAddIndex } from "./contentExtraIndex";
import { app } from "../util/realm";

export default function ContentsList(props) {
  const noteId = props.noteId;
  const cardId = props.cardId;
  const contentState = useContentState();
  const contentDispatch = useContentDispatch();

  const getUserContentPack = async () => {
    const contentpack = await app.currentUser.functions.getUserContentPack({
      userId: app.currentUser.id,
      noteId: noteId,
      cardId: cardId,
    });
    contentDispatch({
      type: "SET_CONTENTS",
      data: contentpack.data.contentPack,
    });
    console.log(contentpack);
  };

  useEffect(() => {
    getUserContentPack();
  }, []);

  return (
    <>
      <ContentAddIndex noteId={noteId} cardId={cardId} />
      <ContentListContainer>
        {contentState.length > 0 ? (
          <>
            {contentState.map((item, index) => (
              <Content
                key={index}
                data={item}
                noteId={noteId}
                cardId={cardId}
              />
            ))}
          </>
        ) : (
          <div>
            wait for new content     
            ...
          </div>
        )}
      </ContentListContainer>
    </>
  );
}
